.hamburger-icon {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 5px;
  width: 35px;
  height: 20px;
  position: relative;
  transition: 0.7s; }
  .hamburger-icon div {
    border-radius: 5px;
    background: white;
    height: 4px;
    transition: 0.5s; }
  .hamburger-icon.close-icon {
    transform: rotate(360deg); }
    .hamburger-icon.close-icon .top {
      transform: rotate(45deg) translate(6px, 8px); }
    .hamburger-icon.close-icon .mid {
      transform: rotate(-45deg); }
    .hamburger-icon.close-icon .bottom {
      transform: rotate(-45deg) translate(6px, -6px); }
