@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700&display=swap");
* {
  margin: 0;
  padding: 0; }

body {
  background: #1d2c3e;
  --text-color: hsl(0, 0%, 90%);
  --highlight: #6947cc;
  box-sizing: border-box;
  overflow-x: hidden; }

.page {
  min-height: 100vh;
  background: linear-gradient(to top right, #19222c, #1d2c3e);
  color: var(--text-color);
  font-family: Roboto;
  overflow: hidden; }
  .page .title {
    margin: 80px 0;
    text-align: center; }
    .page .title h1 {
      font-weight: 300;
      font-size: 3.5em; }
    .page .title h3 {
      font-weight: 100;
      font-size: 2em; }
  .page main {
    max-width: 900px;
    margin: 0 auto; }

a {
  color: var(--text-color);
  text-decoration: none; }

a.normal,
p a:not(.image-label),
footer a {
  position: relative;
  z-index: 2; }
  a.normal:after,
  p a:not(.image-label):after,
  footer a:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 0.4em;
    z-index: -1;
    background: var(--highlight); }
