.post {
  margin: 0 auto; }
  .post > * {
    margin: 0 20px; }
  .post .meta {
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 10px; }
    .post .meta .break {
      margin: 0 10px; }
  .post h2 {
    font-size: 2.5em;
    font-weight: 400;
    margin-bottom: 10px; }
  .post h3 {
    font-weight: 300; }

@media screen and (max-width: 700px) {
  .post h2 {
    font-size: 8vw; } }
